body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* For Mobiles */
body {
  background-color: #2B3A42;
}

.App {
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.content {
  margin: 50px auto;
  width: 90%;
  font-family: 'Courier', Courier, monospace;
  color: #F0F0DF;
  line-height: 32px;
  font-size: 20px;
}

.content a {
  color: #F0F0DF;
}

.content a:hover {
  box-shadow: inset 0 -20px 0 0 #F0F0DF;
  color: #2B3A42;
}

.particles canvas{
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -1;
}

/* For Desktops */

@media only screen and (min-width: 768px) {
  .content {
    margin: 50px auto;
    width: 50%;
    line-height: 40px;
    font-size: 24px;
  }

  .content a:hover {
    box-shadow: inset 0 -24px 0 0 #F0F0DF;
  }
}

